html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html,
body {
  height: 100%; }

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #1c1c1c;
  position: relative; }
  body.fixed {
    overflow: hidden; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: 400; }

a {
  display: inline-block;
  text-decoration: none;
  color: inherit; }

p {
  margin: 0; }

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

button {
  display: block;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer; }

img.dp-block,
svg.dp-block {
  display: block; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.content {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

.container {
  max-width: 1170px;
  width: 100%;
  padding: 0 15px 0;
  margin: 0 auto; }

.title {
  font-size: 70px;
  font-weight: 100;
  font-family: "Roboto", sans-serif;
  line-height: normal;
  text-align: center;
  margin: 130px 0 75px; }

.subtitle {
  font-size: 30px;
  line-height: normal;
  font-weight: 300;
  text-align: center; }

.rotate-arrow {
  transform: rotateZ(180deg); }

.active-box {
  display: block; }

.active-burger .header__burger-item:first-child {
  width: 0; }

.active-burger .header__burger-item:nth-child(2) {
  transform: rotateZ(45deg);
  margin-bottom: -8.5px; }

.active-burger .header__burger-item:last-child {
  margin-bottom: 0;
  transform: rotateZ(-45deg); }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto-regular.woff") format("woff"), url("../fonts/roboto-regular.woff2") format("woff2");
  font-display: swap;
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto-thin.woff") format("woff"), url("../fonts/roboto-thin.woff2") format("woff2");
  font-display: swap;
  font-weight: 100;
  font-style: normal; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto-light.woff") format("woff"), url("../fonts/roboto-light.woff2") format("woff2");
  font-display: swap;
  font-weight: 300;
  font-style: normal; }

.header {
  min-height: 80px;
  border-bottom: 1px solid #d9d9d9; }

.header__nav {
  position: relative;
  padding-top: 35px; }

.header__list {
  display: flex;
  justify-content: center;
  margin-left: -75px; }
  .header__list.active-list {
    display: flex; }

.header__list-item {
  margin-left: 75px; }

.header__list-link {
  font-size: 14px;
  font-weight: 300;
  color: #435993;
  line-height: normal; }
  .header__list-link._active {
    color: #1c1c1c; }

.header__burger {
  display: none;
  position: relative;
  top: 50%;
  right: 0%;
  height: 40px; }

.header__burger-item {
  margin: 5px;
  display: block;
  width: 30px;
  height: 4px;
  background-color: #1c1c1c;
  transition: 0.3s all; }
  .header__burger-item:last-child {
    margin-bottom: 0; }

.personal {
  margin-bottom: 73px; }

.personal__inner {
  display: flex;
  align-items: center;
  flex-direction: column; }

.personal__inner-image {
  margin-bottom: 27px; }

.personal__inner-subtitle {
  margin-bottom: 48px; }

.personal__form {
  display: block;
  text-align: center;
  max-width: 520px;
  width: 100%; }

.personal__form-item {
  position: relative;
  margin-bottom: 25px; }
  .personal__form-item:last-child {
    margin-bottom: 0; }

.personal__form-placeholder {
  text-align: center;
  position: absolute;
  top: 9px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  font-size: 13px;
  font-weight: 400;
  color: #adadad;
  display: none; }

.personal__form-input {
  background-color: transparent;
  width: 100%;
  height: 60px;
  border: 1px solid #e5e5e5;
  outline: none;
  border-radius: 5px;
  text-align: center;
  font-size: 20px;
  font-weight: 400;
  line-height: 19px; }
  .personal__form-input:focus, .personal__form-input:valid {
    padding-top: 15px;
    text-align: center; }
  .personal__form-input:focus {
    border: 1px solid #435993; }
  .personal__form-input:focus + label {
    display: block; }
  .personal__form-input:valid + label {
    display: block; }
  .personal__form-input:focus::-moz-placeholder {
    font-size: 0;
    line-height: 0; }
  .personal__form-input:focus:-ms-input-placeholder {
    font-size: 0;
    line-height: 0; }
  .personal__form-input:focus::placeholder {
    font-size: 0;
    line-height: 0; }
  .personal__form-input::-moz-placeholder {
    color: #c8c8c8;
    font-size: 24px;
    font-weight: 400;
    line-height: normal; }
  .personal__form-input:-ms-input-placeholder {
    color: #c8c8c8;
    font-size: 24px;
    font-weight: 400;
    line-height: normal; }
  .personal__form-input::placeholder {
    color: #c8c8c8;
    font-size: 24px;
    font-weight: 400;
    line-height: normal; }

.personal__select-template {
  margin: 0 auto;
  width: 100%;
  max-width: 321px;
  height: 61px;
  border: 1px solid #e5e5e5;
  background-color: #ffffff;
  position: relative;
  border-radius: 5px; }
  .personal__select-template.select-focus {
    border: 1px solid #435993; }

.personal__select-arrow {
  pointer-events: none;
  position: absolute;
  top: 27px;
  bottom: 27px;
  right: 20px;
  background-image: url(../images/select-appereance.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 13px;
  height: 8px;
  transition: all 0.4s; }
  .personal__select-arrow.rotate-arrow {
    display: block; }

.personal__select-box {
  cursor: pointer;
  display: none;
  max-width: 320px;
  width: 100%;
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  background-color: #ffffff;
  min-height: 301px;
  padding-right: 6px;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
  padding-top: 37px;
  padding-bottom: 32px; }
  .personal__select-box.active-box {
    display: block; }

.personal__select-dropdown {
  height: 301px;
  overflow-y: scroll;
  background-color: #ffffff; }
  .personal__select-dropdown::-webkit-scrollbar {
    width: 5px; }
  .personal__select-dropdown::-webkit-scrollbar-track {
    background-color: #fff;
    width: 4px; }
  .personal__select-dropdown::-webkit-scrollbar-thumb {
    background: #d9d9d9;
    border: 2px solid #d9d9d9;
    border-radius: 20px; }

.personal__select-option {
  color: #435993;
  font-size: 20px;
  font-weight: 300;
  line-height: 36px; }

.personal__select-value {
  font-size: 20px;
  font-weight: 400;
  color: #1c1c1c;
  position: absolute;
  top: 29px;
  left: 80px;
  right: 80px;
  pointer-events: none; }

.personal__select-placeholder {
  position: absolute;
  top: 15px;
  left: 80px;
  right: 80px;
  color: #c8c8c8;
  font-size: 24px;
  font-weight: 400;
  pointer-events: none; }
  .personal__select-placeholder.active-placeholder {
    font-size: 14px;
    font-weight: 400;
    top: 8px; }

.experience {
  margin-bottom: 145px; }

.experience__inner {
  margin-bottom: 58px;
  text-align: center; }

.experience__inner-image {
  margin-bottom: 30px; }

.experience__inner-subtitle {
  margin-bottom: 58px; }

.experience__radio-box {
  margin: 0 auto;
  max-width: 690px;
  width: 100%;
  display: flex;
  justify-content: space-between; }

.experience__radio-label {
  margin-bottom: 15px;
  display: flex;
  align-items: center; }
  .experience__radio-label:last-child {
    margin-bottom: 0; }

.experience__radio-name {
  order: 2;
  color: #000000;
  font-size: 16px;
  font-weight: 300; }

.experience__radio-input:checked + div {
  background-color: #435993; }
  .experience__radio-input:checked + div::after {
    visibility: visible;
    opacity: 1;
    height: 9px; }

.experience__radio-template {
  position: relative;
  margin-right: 12px;
  width: 20px;
  height: 21px;
  border: 1px solid #435993;
  background-color: #ffffff;
  order: 1;
  border-radius: 4px;
  transition: background-color 0.4s; }
  .experience__radio-template::after {
    position: absolute;
    content: "";
    background-image: url(../images/radio-item.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.4s;
    top: 5px;
    left: 3px;
    right: 4px;
    width: 13px;
    height: 0; }
  .experience__radio-template:hover {
    background-color: #e9efff; }

.javascript {
  margin-bottom: 72px; }

.javascript__inner {
  text-align: center; }

.javascript__inner-image {
  margin-bottom: 30px; }

.javascript__inner-subtitle {
  margin-bottom: 47px; }

.javascript__range {
  margin: 0 auto;
  position: relative;
  max-width: 770px;
  width: 100%; }

.javascript__range-active {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  background-image: url(../images/active-range.svg);
  max-width: 770px;
  height: 15px;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat; }

.javascript__range-line {
  cursor: pointer;
  max-width: 770px;
  width: 100%;
  border: 0;
  -webkit-appearance: none;
  outline: none; }

.javascript__range-line::-webkit-slider-thumb {
  -webkit-appearance: none;
  margin-top: 15px;
  width: 25px;
  height: 28px;
  background-image: url(../images/range-drug.svg);
  cursor: pointer; }

.javascript__range-line::-webkit-slider-runnable-track {
  background-size: cover;
  -webkit-appearance: none;
  background-image: url(../images/not-active-range.svg);
  max-width: 770px;
  cursor: pointer;
  height: 15px; }

.javascript__marks {
  position: relative;
  width: 100%;
  margin: 0 auto 16px;
  max-width: 770px;
  height: 32px; }

.javascript__marks-item {
  position: absolute;
  font-size: 13px;
  font-weight: 300;
  text-align: left; }
  .javascript__marks-item:first-child {
    left: 0;
    bottom: 0; }
  .javascript__marks-item:nth-child(2) {
    left: 25%; }
  .javascript__marks-item:nth-child(3) {
    left: 50%; }
  .javascript__marks-item:last-child {
    left: 80%;
    bottom: 0; }

.about {
  margin-top: 72px;
  margin-bottom: 200px; }

.about__inner {
  text-align: center; }

.about__inner-subtitle {
  margin-bottom: 32px; }

.about__inner-text {
  margin: 0 auto 39px;
  font-size: 16px;
  font-weight: 300;
  max-width: 500px;
  width: 100%; }

.about__inner-textarea {
  width: 100%;
  padding-left: 20px;
  padding-top: 25px;
  max-width: 521px;
  min-height: 181px;
  border: 1px solid #e5e5e5;
  resize: none;
  color: #1c1c1c;
  font-size: 20px;
  font-weight: 400;
  outline: none;
  border-radius: 4px; }
  .about__inner-textarea:focus {
    border: 1px solid #435993; }
  .about__inner-textarea::-moz-placeholder {
    font-weight: 400;
    font-size: 20px;
    color: #c8c8c8; }
  .about__inner-textarea:-ms-input-placeholder {
    font-weight: 400;
    font-size: 20px;
    color: #c8c8c8; }
  .about__inner-textarea::placeholder {
    font-weight: 400;
    font-size: 20px;
    color: #c8c8c8; }

@media (max-width: 768px) {
  .title {
    margin-top: 60px;
    font-size: 28px; }
  .subtitle {
    font-size: 22px; }
  .header__burger {
    display: block; }
  .header__list {
    display: none;
    flex-direction: column; }
  .header__list-item {
    margin-bottom: 20px; }
  .experience {
    margin-bottom: 60px; }
  .javascript__marks {
    display: none; }
  .experience__radio-box {
    display: flex;
    flex-direction: column; }
  .experience__radio-item {
    margin-bottom: 30px; }
  .personal__select-placeholder {
    font-size: 18px; }
  .personal__form-input::-moz-placeholder, .about__inner-textarea::-moz-placeholder {
    font-size: 20px; }
  .personal__form-input:-ms-input-placeholder, .about__inner-textarea:-ms-input-placeholder {
    font-size: 20px; }
  .personal__form-input::placeholder,
  .about__inner-textarea::placeholder {
    font-size: 20px; } }
